// Custom variables (including functions and mix-ins)
// E.g., p { @import font-size($h1-font-size); }
@import "~themes/bootstrap-variables";

.component {

  :global {
    // Global class selectors go here.
    // Applied to all DOM elements nested within this component.
    p, li {
      font-weight: 500;
      color:rgba($black, 0.5);
      line-height: 1.5;
    }

    strong {
      color: black;
    }
  }
}

// Local class selectors go here. 
// Applied to only DOM elements directly defined in this component.

.stats {
  margin: 1rem auto;
  width: 16rem;
  height: 16rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.75rem solid $primary;
  border-radius: 999px;
  text-align: center;
}

.mask {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: $primary;
  opacity: 0.7;
  mix-blend-mode: color;
}

.farmer {
  width: 10rem;
  height: 10rem;
  border-radius: 999px;
}


.service {
  width: 50%;
  max-width: 10rem;
  display: block;
  padding: 0.5rem;
  z-index: 1;
}

.footer {
  height: 20rem;
  background: rgba($black, 0.7) url('./footer.jpg') no-repeat center center;
  background-size: cover;
  background-blend-mode: multiply;
}
